import Button from "./Button";
import InputBox from "./InputBox";
import RadioInput from "./RadioInput";
import TextareaBox from "./TextareaBox";
import { useModal } from "../contexts/modalContext";
import useForm from "../hooks/useForm";
import { useToast } from "../contexts/toastContext";
import { useAddress } from "../contexts/addressContext";
import { isNameValid, isPhoneValid, isPinCodeValid } from "../utils/validation";

const AddressForm = ({ initialAddress = {}, edit = false }) => {
  const toast = useToast();
  const { closeModal } = useModal();
  const { addAddress, updateAddress, loading } = useAddress();

  const onSubmitHandler = async (addressData) => {
    const {
      _id,
      addressType,
      receiverName,
      receiverPhone,
      pinCode,
      locality,
      city,
      state,
    } = addressData;

    // Client side validations
    if (!addressType) {
      toast.error("Please select type of address");
      return;
    } else if (!isNameValid(receiverName).success) {
      toast.error(isNameValid(receiverName).message);
      return;
    } else if (!isPhoneValid(receiverPhone).success) {
      toast.error(isPhoneValid(receiverPhone).message);
      return;
    } else if (!isPinCodeValid(pinCode).success) {
      toast.error(isPinCodeValid(pinCode).message);
      return;
    } else if (!locality) {
      toast.error("Please enter locality");
      return;
    } else if (!city) {
      toast.error("Please enter city");
      return;
    } else if (!state) {
      toast.error("Please enter state");
      return;
    }

    if (edit) {
      updateAddress(_id, addressData);
      if (!loading) {
        clearForm();
        closeModal();
      }
    } else {
      addAddress(addressData);
      if (!loading) {
        clearForm();
        closeModal();
      }
    }
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    initialAddress,
    onSubmitHandler
  );

  return (
    <div className="w-[420px] h-[620px] shadow-primary bg-white border border-primary rounded-xl p-6">
      <h2 className="capitalize text-2xl font-medium text-shadow-lg text-center mb-6">
        {!edit ? "add address" : "edit address"}
      </h2>
      <form onSubmit={handleSubmit}>
        <div className="RADIO flex justify-center items-center gap-6">
          <RadioInput
            label="home"
            name="addressType"
            value="home"
            checked={formData.addressType === "home"}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <RadioInput
            label="office"
            name="addressType"
            value="office"
            checked={formData.addressType === "office"}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <RadioInput
            label="others"
            name="addressType"
            value="others"
            checked={formData.addressType === "others"}
            onChange={handleChange}
            containerClassName="gap-1"
          />
        </div>
        <div className="grid grid-cols-2 gap-x-6 gap-y-2 mt-4 my-2">
          <InputBox
            label="enter name"
            type="text"
            name="receiverName"
            value={formData.receiverName}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="mobile no"
            type="tel"
            name="receiverPhone"
            value={formData.receiverPhone}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="pin code"
            type="text"
            name="pinCode"
            value={formData.pinCode}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="house no"
            type="text"
            name="houseNumber"
            value={formData.houseNumber}
            onChange={handleChange}
            containerClassName="gap-1"
          />
        </div>
        <div className="space-y-2">
          <TextareaBox
            label="street"
            type="text"
            name="street"
            value={formData.street}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <TextareaBox
            label="locality"
            type="text"
            name="locality"
            value={formData.locality}
            onChange={handleChange}
            containerClassName="gap-1"
          />
        </div>
        <div className="grid grid-cols-2 gap-x-6 gap-y-3 my-2">
          <InputBox
            label="city"
            type="text"
            name="city"
            value={formData.city}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="state"
            type="text"
            name="state"
            value={formData.state}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="landmark"
            type="text"
            name="landmark"
            value={formData.landmark}
            onChange={handleChange}
            containerClassName="gap-1"
          />
          <InputBox
            label="alternate number"
            type="tel"
            placeholder="alternate mobile (optional)"
            name="alternatePhone"
            value={formData.alternatePhone}
            onChange={handleChange}
            containerClassName="gap-1"
          />
        </div>
        <Button className="mt-5 px-6 py-2 rounded-full font-secondary mx-auto">
          save
        </Button>
      </form>
    </div>
  );
};

export default AddressForm;
