import { FaCirclePlus, FaPhone } from "react-icons/fa6";
import Button from "./Button";
import { useModal } from "../contexts/modalContext";
import AddressForm from "./AddressForm";
import { FiTrash2 } from "react-icons/fi";
import { useAddress } from "../contexts/addressContext";

const AddressBox = ({ address }) => {
  const { openModal } = useModal();
  const { setDefault, removeAddress } = useAddress();

  const handleDefaultAddress = () => {
    setDefault(address._id);
  };

  const handleDeleteAddress = () => {
    removeAddress(address._id);
  };

  return (
    <div className="w-64 max-sm:w-full h-44 max-sm:h-auto border border-dashed border-darkGray rounded-md flex flex-shrink-0 justify-center items-center cursor-pointer bg-white overflow-hidden">
      {!address ? (
        <div
          onClick={() => openModal(<AddressForm />)}
          className="NO-ADDRESS size-full text-primeRed flex sm:flex-col justify-center items-center gap-y-2 gap-x-4 hover:bg-offWhite p-4"
        >
          <FaCirclePlus size={24} />
          <p className="capitalize text-lg font-secondary font-medium">
            add new address
          </p>
        </div>
      ) : (
        <div className="HAVE=-ADDRESS relative size-full p-3 flex flex-col items-start">
          {address?.isDefault && (
            <span className="absolute top-3 right-3 text-[8px] text-white font-medium px-1 rounded-sm pt-[1px] bg-success">
              DEFAULT
            </span>
          )}
          <div className="flex items-center justify-center gap-2 font-medium leading-tight pr-12">
            <h3>{address?.receiverName}</h3>
            <p className="uppercase text-[8px] px-1 py-[1px] h-max font-semibold bg-mainGray rounded-sm">
              {address?.addressType}
            </p>
          </div>
          <div className="flex justify-start items-center gap-2 text-xs text-secondary">
            <FaPhone />
            <p>{address?.receiverPhone || "N/A"}</p>
            {address?.alternatePhone && (
              <p>
                <span className="mx-1">/</span>
                {address.alternatePhone}
              </p>
            )}
          </div>

          <div className="text-sm font-medium text-secondary flex flex-wrap gap-x-1 mt-2">
            {address?.houseNumber && <p>{address.houseNumber},</p>}
            {address?.street && <p>{address.street},</p>}
            {address?.locality && <p>{address.locality},</p>}
            {address?.landmark && <p>{address.landmark},</p>}
            {address?.city && <p>{address.city},</p>}
            {address?.state && <p>{address.state},</p>}
            {address?.pinCode && <p>{address.pinCode}</p>}
          </div>

          <div className="BTN-SECTION w-full flex justify-between items-center gap-2 mt-auto max-sm:mt-4">
            <div className="flex items-center gap-4">
              <Button
                reverse
                onClick={() =>
                  openModal(<AddressForm initialAddress={address} edit />)
                }
                className="h-6 px-3 py-1 text-xs rounded-md font-secondary font-medium"
              >
                Edit
              </Button>
              {!address?.isDefault && (
                <Button
                  onClick={handleDefaultAddress}
                  className="h-6 text-xs px-6 py-1 rounded-md font-secondary"
                >
                  set as default
                </Button>
              )}
            </div>
            <button onClick={handleDeleteAddress}>
              <FiTrash2 size={18} className="text-primeRed" />
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default AddressBox;
